.lang__select {
    appearance: none;
    background-color: var(--body-color);
    box-shadow: none !important;
    width: 32px;
    height: 20px;
    font-size: 20px;
    overflow: none;
}

@media screen and (max-width: 768px) {
    .lang__select {
        position: absolute;
        bottom: 0;
        top: 80%;
        right: 0;
        left: 0;
        width: 42px;
        height: 42px;
        font-size: 34px;
    }
}