:root {
    --primary-light: #8abdff;
    --primary: #6d5dfc;
    --primary-dark: #5b0eeb;
    
    --white: #FFFFFF;
    --greyLight-2: #c8d0e7;
    --greyDark: #9baacf;
}

/* SWITCH */
.switch input {
    display: none;
}
.darkbutton {
    width: 1.5rem;
}
.darkbutton label {
    display: flex;
    align-items: center;
    width: 100%;
    height: 1.5rem;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--greyLight-2), -0.2rem -0.2rem 0.5rem var(--white);
    background: rgb(0, 0, 0);
    position: relative;
    cursor: pointer;
    border-radius: 1.6rem;
}
.darkbutton label::after {
    content: "";
    position: absolute;
    left: 1rem;
    width: 2.1rem;
    height: 2.1rem;
    border-radius: 50%;
    transition: all 0.4s ease;
}
.darkbutton label::before {
    content: "";
    border-radius: inherit;
    background: linear-gradient(330deg, var(--primary-dark) 0%, var(--primary) 50%, var(--primary-light) 100%);
    opacity: 1;
    transition: all 0.4s ease;
}
.switch input:checked ~ label::before {
    opacity: 1;
    background: rgb(255, 255, 255);
}